
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {
components:{
Datetime,
PopupPicker,
Group,
Cell,
Picker,
Divider,
XSwitch
},
data(){
return{
bottomtext:'保存并退出',
bottomdis:'none',
topertext:"",
toperimg:"img/ing.png",
zt:4,
lqindex:0,
array:["完税凭证邮寄方式","自取","邮寄"],
color:"#CCCCCC",
lqdzdis:"true",
yjdis:"true",
cgdis:"true",
sjr:"",
sjhm:"",
lqdz:"",
applyId:"",
back:"#CDD4E0",
nametip:true,
idtip:true
}
},
methods:{
bindPickerChange:function(e){
var i = e.detail.value;
if(i==0){
this.data.color = "#CCCCCC;"
this.data.lqindex = i;
this.data.lqdzdis = "none";
this.data.yjdis="none";
}else if(i==1){
this.data.color = "#666666";
this.data.lqindex = i;
this.data.lqdzdis = "block";
this.data.yjdis = "none";
}else{
this.data.color = "#666666";
this.data.lqindex = i;
this.data.lqdzdis = "none";
this.data.yjdis = "block";
}
this.setData(this.data);
sfgl(this);
},
return1:function(){
if (this.data.lqindex == 2 && this.data.sjr != "" && this.data.sjhm != "" && this.data.lqdz != ""){
var url = commonJs.url;
var that = this;
that.$router.push('index');
} else if (this.data.topertext != "支付成功" && this.data.lqindex != 1 ){
let path="/ProcessManage/Process09"
this.$router.push({
path: path,
query:{applyId:applyId,clientKey:""}
});



} else if (this.data.lqindex == 1 ){
var url = commonJs.url;
var that = this;
wx.showModal({
title: '警告！',
content: '一旦选择自取将无法再选择邮寄，确认要自取么？',
confirmText: '确认',
cancelText: '取消',
success: function (res) {
if (res.confirm) {
wx.request({
url: url + "/qishui/qsapp/api/apply/zq",
method: "GET",
header: commonJs.getHeader(that.data.clientKey),
data: {
applyId: that.data.applyId,
},
success: function () {
}
})
wx.showToast({
title: '选择自取成功！',
icon: "none",
duration: 2000,
})
wx.navigateBack({
delta: 1,
})
}
}
})

}else {
this.setData({
back: "#CDD4E0",
})
}

},
sjrinput:function(e){
if (commonJs.xm(e.detail.value) || e.detail.value==""){
this.setData({
nametip:true
})
}else{
this.setData({
nametip: false
})
}
this.setData({
sjr: e.detail.value
});
sfgl(this);
},
sjhminput:function(e){
if (commonJs.phone(e.detail.value) || e.detail.value == "") {
this.setData({
idtip: true
})
} else {
this.setData({
idtip: false
})
}
this.setData({
sjhm:e.detail.value
});
sfgl(this);
},
lqdzinput:function(e){
this.setData({
lqdz:e.detail.value
});
sfgl(this);
}
}
}



