import Common from "../../lib/common.js"
export default {
    data(){
        return {
          isTrue:false,

          userInfo:{headPic: require('@/assets/static/images/touxiang.jpg'),userName:'默认'},
          text:[
            {
                text: '我的业务',
                url: 'Process',
                tip: '',
                icon:'el-icon-share',
            },
            {
              text: '交易状态查询',
              url: 'Deal',
              tip: '',
              icon:'el-icon-document',
            },
              {
              text: '我的快递单号',
              url: 'PostPage',
              tip: '',
              icon:'el-icon-zoom-in',
            },
            //     {
            //     text: '在缴一笔',
            //     url: 'payAgain',
            //     tip: ''
            //   },
            {
              text: '联系我们',
              url: 'Help',
              tip: '',
              icon:'el-icon-phone',
            },
              {
                  text: '测试图片压缩',
                  url: 'uploadImg',
                  tip: '',
                  icon:'el-icon-phone',
              }
          ]
        }
    },
    methods:{
      clickRoute(){
        this.$emit('handerSelect',4);  //子组建触发父组建方法
      },
        loading(that,data){
            let arr = [data];
            let str = arr.join('</br>');
            that.$message({
                dangerouslyUseHTMLString:true,
                message:str,
                type:'warning'
            })
        },
        reload(){
          var that = this;
            Common.reload(that);
        },
        payAgain(){
          var that = this;
            this.$confirm('此次操作将覆盖之前的记录，您确认操作吗?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                Common.payAgain(function(res){
                    if(res.data.rows[0].code=='200'){
                        that.loading(that,"操作成功");
                    }else if(res.data.rows[0].code=='201'){
                        that.loading(that,"请您先完成当前交易");
                    }else{
                        that.loading(that,"不允许操作");
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '用户已取消'
                });
            });
        }
    },
    mounted() {
      var that = this;
      Common.FindUser(function (res) {
        that.userInfo.userName = res.data;
        if(res.data==='宋俊松'|| res.data==='默认1'||res.data ==='胡小天'
            ||res.data ==='谢天培'||res.data ==='李大江'||res.data ==='邹念超'){
            that.isTrue=true;
        }
      },function () {

      })
    }
}



