
import XInput from 'vux/src/components/x-input/index.vue'

import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {
components: {
PopupPicker,
Group,
Picker,
XButton,
Divider,
Cell,
XSwitch,
Datetime,
XInput
},
data(){
return {
yzmhidden:true,
djssize:'30rpx',
disabled:true,
value:[''],
array: [["选择你的银行卡", "中国建设银行", "乐山市商业银行", "中国邮政银行","中国招商银行"]],
index:0,
cardclass:"inputpla",
text:[
{
name:"卡　号",
pla:"填写你的银行卡号",
value:"",
tip:true,
tipval:"*银行卡号不正确",
type1:'number',
maxlength:19,
focus: true,
tap:'tap'
},
{
name: "姓　名",
pla: "持卡人姓名",
value: "",
tip: true,
tipval: "*姓名不正确",
type1:'text',
maxlength:12,
focus: false,
tap: 'tap1'
},
{
name: "身份证",
pla: "持卡人身份证",
value: "",
tip: true,
tipval: "*身份证不正确",
type1:'text',
maxlength:18,
focus: false,
tap: 'tap1'
}
],
cardid:"",
banktype:"",
bankphone:"",
yzm:"",
back:"#cdd4df",
djs: 60,
djstxt: "获取验证码",
djscolor: "#1c3e72",
djstap: "djs",
width:"300rpx",
tip:true,
}
},
methods:{
nexttext(){
if (this.data.length){
if (this.data.back =="#1c3e72"){
window.history(-1)
}

}

},
bindPickerChange(value){
var i = value[0];
var that = this;

console.log(that.text[0])
if (this.index != 0 && that.text[0].value != ""){
wx.showModal({
title: '提示：',
content: '更换银行卡会导致清空当前卡号',
success: function(res) {
if(res.confirm){
that.text[0].value = "";
that.text[0].tip = true;
}
},
})
}else{
this.text[0].value = "";
this.text[0].tip = true;
}
if(i==0){
this.text[0].focus=true;
this.text[0].tap='tap';
this.cardclass = "inputpla";
this.setData(this.data);
}else{
this.text[0].focus = false;
this.text[0].tap = 'tap1';
this.cardclass = "inputpla1";
}
switch(i){
case "0":
this.data.cardtype="";
break;
case "1":
this.data.cardtype = "中国建设银行";
break;
case "2":
this.data.cardtype = "乐山市商业银行";
break;
case "3":
this.data.cardtype = "中国邮政银行";
break;
case "4":
this.data.cardtype = "中国招商银行";
break;
}
this.index=value
sfgl(this);
},
yzmtc: function () {







},
tap:function(e){
var i = e.currentTarget.dataset.index;
if (i == "0" || i == 0){
wx.showToast({
title: '您还没有选择开户行',
icon: "none",
duration: 2000,
})
}
},
tap1:function(){

},
lose:function(index){
var i =index;
switch (i) {
case 0:
case "0":
this.text[i].tip = true;
break;
case 1:
case "1":
this.text[i].tip = true;
break;
case 2:
case "2":
this.text[i].tip = true;
break;
default:
this.tip = true;
break;
}
},
input(index){
this.data.text[i].value =this.cardid;
switch(i){
case 0:
case "0":
if (!commonJs.yhkh(this.cardid) && this.cardid != ""){
this.text[i].tip = false;
this.text[i].value = this.cardid;
}else{
this.text[i].tip = true;
this.text[i].value = this.cardid;
}
break;
case 1:
case "1":
if (!commonJs.xm(this.cardid) && this.cardid != "") {
this.text[i].tip = false;
this.text[i].value =this.cardid;
} else {
this.text[i].tip = true;
this.text[i].value = this.cardid;
}
break;
case 2:
case "2":
if (!commonJs.idcard(e.detail.value) && e.detail.value != "") {
this.text[i].tip = false;
} else {
this.text[i].tip = true;
}
break;
}
this.setData(this.data);
sfgl(this);
},
inputphone:function(e){
if (!commonJs.phone(this.bankphone) && this.bankphone!="") {
this.tip = false;
this.yzmhidden = true;
}else if(!commonJs.phone(this.bankphone) && this.bankphone==""){
this.data.tip = true;
this.data.yzmhidden = true;
} else {
this.tip = true;
this.yzmhidden=false;
}
sfgl(this);
},
inputyzm:function(e){
sfgl(this);
},
}
}



