import Common from "@/lib/common";
export default {
  name: "Agree",
  data(){
    return{
      isAble:true,
      applyId:"",
      data:{id:''}
    }
  },
  mounted(){
      if (this.$route.query && this.$route.query.data) {
        var data = this.$route.query.data
        this.data.id = data.id;
    }
  },
  methods:{
    checked:function(e){
      let isChecked = e.target.checked;
      if(isChecked === true){
        this.isAble = false;
      }else{
        this.isAble = true;
      }
    },
    gotofg(){
      Common.Sqtg(this.data);
      this.$router.push({
        path:"SyncFgyInfo",
        query: {data:this.data}
      });
    }
  }
}



