import SwipperImage from "@/components/CommComponents/SwipperImage"
import Common from "@/lib/common"
export default {
     components:{
        SwipperImage
    },
    data(){
        return{
            bright: ['', '', '', ''],
            swiperimgs: [
                {
                    url: 'javascript',
                  img: require('@/assets/static/images/1.jpg')
                },
                {
                    url: 'javascript',
                    img: require('@/assets/static/images/a1.jpg')
                },
                {
                    url: 'javascript',
                    img: require('@/assets/static/images/a2.jpg')
                }
            ],
            text: [
                {
                    text: '自助办理',
                    url: 'personal',
                    tip: '',
                    sclass: 'go',
                    img: 'img/personal.png'
                },
            ],
            goitem: [

                {
                    border: '0rpx 1rpx 0rpx 0rpx',
                    first: '资料审核',
                    secend: '在家体验便民服务',
                    src: require('@/components/BusinessHandManage/img/BusinessManagement.png'),
                    url: 'personal',
                    hidden:false
                },
                {
                    border: '0rpx 0rpx 0rpx 1rpx',
                    first: '契税缴费',
                    secend: '方便快捷',
                    src: require('@/components/BusinessHandManage/img/synchronization.png'),
                    url: 'none',
                    hidden: false
                }
                // ,
                // {
                //     border: '0rpx 0rpx 0rpx 1rpx',
                //     first: '房产数据',
                //     secend: '一次授权自动同步',
                //     src: require('@/components/BusinessHandManage/img/synchronization.png'),
                //     url: 'none',
                //     hidden: false
                // }

            ],
            clientKey: "",
            backhidden:false,
            kydjtimeout:'',
            gotoUrl:''
        }
    },
    methods:{
         bright1(key){
            var i = key
            this.bright[i] = 'bright';
        },
        normal:function(key){
            var i = key
            this.bright[i] = '';
        },
        personal(val){
           if(val=="personal"){
             var that = this
             Common.FindUser(function (){
               Common.Spjl(function (data) {
                 that.$router.push({
                   path:"process",
                   query: {data:data}
                 });
               });
             },function () {
               that.$router.push({
                 path:"login",
                 params: {}
               });
             });
           }else{
               var that1 = this
               Common.FindUser(function (){
                   Common.Spjl(function (data) {
                     if(data.spzt=="3"){
                         that1.$router.push({
                             path:"/ProcessManage/Process09",
                             params: {}
                         });
                     }else{
                         if(data.spzt=="8" ||data.spzt=="9"){
                             Common.loading(that1,'费用已缴纳，请勿重复提交');

                         }else if(data.spzt=="1"){
                             Common.loading(that1,'正在查看您的缴税资料，暂时不能进行缴费操作');
                         }
                         else{
                             Common.loading(that1,'请先在资料审核中填写相信息，税务局审核后才可以缴费哦~');

                         }
                         that1.$router.push({
                             path:"index",
                             params: {}
                         });
                     }
                   });
               },function () {
                   that.$router.push({
                       path:"index",
                       params: {}
                   });
               });



             //跳转到房管数据同步接口
             // var that1 = this
             // Common.FindUser(function (){
             //   Common.Spjl(function (data) {
             //       that1.$router.push({
             //         path:"Agree",
             //         query: {data:data}
             //       });
             //   });
             // }, function () {
             //   that.$router.push({
             //     path: "login",
             //     params: {}
             //   });
             // });
           }

        },

    },
    mounted(){

    }
}



