
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import Common from '@/lib/common'
import imageConversion from 'image-conversion'

export default {
components: {
PopupPicker,
Group,
Picker,
XButton,
Divider,
Cell,
XSwitch,
Datetime
},
props:{
cards:{
type:Array,
default:null
}
},
data(){
return{
sclass: "select",
dclass: "select",
value:[''],
dtext: '离异时间',
bkcz:'none',
dhidden: true,
backhidden:true,
array: [['现在的婚姻状况', '已婚', '未婚', '离异',"丧偶"]],
phototext: '已婚需上传结婚证，离异需上传离婚证（如有法院判决书则需一并上传），未婚需上传未婚声明。',
hhindex: 0,
cango1: "0",
cango2: "0",
back: "#cdd4df",
addhidden:true,
processLength:0,
showProcess:false
}
},
computed:{
date(){
var year  = this.now().substring(0, 4) + "年"
var month= this.now().substring(5, 7) + "月"
var day=this.now().substring(8, 10) + "日"
return year+month+day
},
enddate(){
this.now()
},
startdate(){
this.starttime()
}
},
mounted(){

},
methods:{
beforeUpload (file) {
return new Promise((resolve, reject) => {
let isLt2M = file.size / 1024 / 1024 < 4
if (isLt2M) {
resolve(file)
}
console.log(file)
imageConversion.compressAccurately(file, 400).then(res => {
resolve(res)
})
})
},
handleChange(file,fileList){
if(file.status === 'ready'){
this.processLength = 0
this.showProcess = true
const interval = setInterval(()=>{
if(this.processLength>=99){
clearInterval(interval)
return
}
this.processLength +=1
},20)
}
if(file.status === 'success'){
this.processLength = 100
this.showProcess = false
}
},
photo(i,index){
Common.seeImg(i,index,this);
},
upfile:function(response, file, fileList,i,index){
if(i==1&&this.cards[0].files[0].flowFileUrl==""){
i=0;
}
Common.upfile(response,i,index,this);
console.log(response);
},
jlhzt(value){
var value=value[0];
if (value == "现在的婚姻状况") {
this.sclass="select"
this.dhidden=true
this.phototext='已婚需上传结婚证，离异需上传离婚证（如有法院判决书则需一并上传）'
return;
} else if (value == "已婚") {
this.hhindex = 1;
this.sclass= "select2";
this.dtext= "结婚时间";
this.dhidden= false;
this.phototext= '请上传结婚证';
this.cards[0].image1dis="block";
this.addhidden=false;
} else if (value == "离异") {
this.hhindex = 3;
this.sclass= "select2";
this.dtext= "离婚时间";
this.dhidden=false;
this.phototext= '请上传离婚证（如有法院判决书则需一并上传）。'
this.cards[0].image1dis="block";
this.addhidden=false;
} else if (value == "丧偶") {
this.hhindex = 4
this.sclass= "select2",
this.dhidden= true,
this.phototext= ''
} else {
this.hhindex = 2
this.sclass= "select2",
this.dhidden= true,
this.phototext= ''
}
Common.upValue(value,0, 0,"婚姻状况",this);
},
changeTime(value){
console.log(value);
Common.upValue(value,0, 1,this.dtext,this);
},

jlhsj(that,date){
this.dclass="select2",
this.date=date
},
now(){
var i = 0;
var timestamp = Date.parse(new Date());
timestamp = timestamp / 1000;

var n = timestamp * 1000;
var date = new Date(n);

var Y = date.getFullYear();

var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);

var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
return Y + '-' + M + '-' + D
},
starttime(){
var i = 0;
var timestamp = Date.parse(new Date());
timestamp = timestamp / 1000;

var n = timestamp * 1000;
var date = new Date(n);

var Y = date.getFullYear();

var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);

var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
return (Y-80) + '-' + M + '-' + D
},
},
deleteimg(index){
var i = index;
var that = this;
wx.showModal({
title: '提示',
content: '您确定要删除么？',
success:function(res){
if(res.confirm){
commonJs.deleteonephoto(that, i,function(){
sfgl(that);
});
}
}
})
},
next(){

}
}



